import type { MouseEvent } from "react";
import { Facebook, Instagram, Linkedin, Mail, Phone } from "lucide-react";
const logoImage = "/assets/daf712f9ccd73d3e18d8aa73235d6b4d79068111.png";

type MenuItem = {
  id: number;
  title: string;
  url: string;
  target?: string;
  order?: number;
};

type LinkItem = {
  label: string;
  url: string;
};

type SocialLink = {
  label: string;
  url: string;
  icon?: string;
};

type FooterProps = {
  menuItems?: MenuItem[];
  logoUrl?: string;
  siteName?: string;
  description?: string;
  email?: string;
  phone?: string;
  notice?: string;
  legalLinks?: LinkItem[];
  socialLinks?: SocialLink[];
};

const socialIconMap: Record<string, typeof Facebook> = {
  facebook: Facebook,
  instagram: Instagram,
  linkedin: Linkedin,
};

export function Footer({
  menuItems,
  logoUrl,
  siteName,
  description,
  email,
  phone,
  notice,
  legalLinks,
  socialLinks,
}: FooterProps) {
  const currentYear = new Date().getFullYear();

  const quickLinks = menuItems?.length
    ? [...menuItems]
        .sort((a, b) => (a.order ?? 0) - (b.order ?? 0))
        .map((item) => ({
          label: item.title,
          href: item.url,
          target: item.target,
        }))
    : [
        { label: "Inicio", href: "#inicio", target: "_self" },
        { label: "Proceso", href: "#proceso", target: "_self" },
        { label: "Caso de Éxito", href: "#caso-exito", target: "_self" },
        { label: "Por qué elegirnos", href: "#por-que", target: "_self" },
      ];

  const resolvedLegalLinks =
    legalLinks?.length &&
    legalLinks.map((link) => ({ label: link.label, href: link.url }));

  const resolvedLogo = logoUrl || logoImage;
  const resolvedName = siteName || "nomasarriendo.cl";
  const resolvedDescription =
    description ||
    "Tu aliado en el proceso de compra de tu nuevo hogar. Acompañamiento profesional y personalizado desde la primera consulta hasta las llaves.";
  const resolvedEmail = email || "contacto@nomasarriendo.cl";
  const resolvedPhone = phone || "+56 2 2345 6789";
  const resolvedNotice =
    notice ||
    "nomasarriendo.cl es una empresa de asesorías inmobiliarias. No somos corredores de propiedades ni ofrecemos servicios de domicilio. Nuestros servicios se enfocan en la consultoría y acompañamiento profesional en procesos de compra habitacional.";
  const resolvedSocialLinks = socialLinks?.length
    ? socialLinks
    : [
        { label: "Facebook", url: "#", icon: "facebook" },
        { label: "Instagram", url: "#", icon: "instagram" },
        { label: "LinkedIn", url: "#", icon: "linkedin" },
      ];

  const handleScroll = (e: MouseEvent<HTMLAnchorElement>, href: string) => {
    if (href.startsWith("#")) {
      e.preventDefault();
      const element = document.querySelector(href);
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
      }
    }
  };

  return (
    <footer className="bg-gray-900 text-white">
      <div className="container mx-auto px-6 lg:px-8">
        {/* Main Footer Content */}
        <div className="py-12 lg:py-16">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 lg:gap-12">
            {/* Company Info */}
            <div className="space-y-6">
              <div className="flex items-center space-x-3">
                <img
                  src={resolvedLogo}
                  alt="nomasarriendo.cl logo"
                  className="w-12 h-12 bg-white rounded-full p-1"
                />
                <span className="text-xl font-semibold">{resolvedName}</span>
              </div>

              <p className="text-gray-400 leading-relaxed">
                {resolvedDescription}
              </p>

              <div className="space-y-3">
                <div className="flex items-center text-gray-400">
                  <Mail className="w-5 h-5 mr-3" />
                  <span>{resolvedEmail}</span>
                </div>
                <div className="flex items-center text-gray-400">
                  <Phone className="w-5 h-5 mr-3" />
                  <span>{resolvedPhone}</span>
                </div>
              </div>
            </div>

            {/* Quick Links */}
            <div>
              <h3 className="text-lg font-semibold mb-6">Enlaces rápidos</h3>
              <ul className="space-y-3">
                {quickLinks.map((link) => (
                  <li key={link.label}>
                    <a
                      href={link.href}
                      onClick={(e) => handleScroll(e, link.href)}
                      target={link.target || undefined}
                      rel={link.target === "_blank" ? "noreferrer" : undefined}
                      className="text-gray-400 hover:text-white transition-colors duration-200"
                    >
                      {link.label}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            {/* Services */}
            <div>
              <h3 className="text-lg font-semibold mb-6">Servicios</h3>
              <ul className="space-y-3 text-gray-400">
                <li>Asesoría financiera</li>
                <li>Búsqueda de propiedades</li>
                <li>Gestión de créditos</li>
                <li>Acompañamiento legal</li>
                <li>Post-venta</li>
              </ul>
            </div>

            {/* Contact & Social */}
            <div>
              <h3 className="text-lg font-semibold mb-6">Síguenos</h3>

              <div className="flex space-x-4 mb-6">
                {resolvedSocialLinks.map((link) => {
                  const Icon =
                    socialIconMap[(link.icon || "").toLowerCase()] || Facebook;
                  return (
                    <a
                      key={link.label}
                      href={link.url}
                      className="w-10 h-10 bg-gray-800 rounded-full flex items-center justify-center hover:bg-blue-600 transition-colors duration-200"
                      aria-label={link.label}
                      target="_blank"
                      rel="noreferrer"
                    >
                      <Icon className="w-5 h-5" />
                    </a>
                  );
                })}
              </div>

              <div className="brand-gradient p-4 rounded-lg">
                <h4 className="font-semibold mb-2">¿Listo para empezar?</h4>
                <p className="text-brand-cream text-sm mb-3">
                  Agenda tu evaluación gratuita hoy
                </p>
                <button
                  className="bg-white text-brand-brown px-4 py-2 rounded font-semibold text-sm hover:bg-brand-cream transition-colors duration-200 w-full"
                  onClick={() =>
                    document
                      .querySelector("#contacto")
                      ?.scrollIntoView({ behavior: "smooth" })
                  }
                >
                  Contactar ahora
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Bottom Bar */}
        <div className="border-t border-gray-800 py-8">
          <div className="flex flex-col md:flex-row justify-center items-center space-y-4 md:space-y-0">
            {/* Copyright */}
            <div
              className={
                `text-gray-400 text-sm flex-1` +
                (resolvedLegalLinks ? " text-left" : " text-center")
              }
            >
              © {currentYear} nomasarriendo.cl. Todos los derechos reservados.
            </div>

            {/* Legal Links */}
            {resolvedLegalLinks && (
              <div className="flex flex-wrap items-center space-x-6 text-sm">
                {resolvedLegalLinks.map((link) => (
                  <a
                    key={link.label}
                    href={link.href}
                    className="text-gray-400 hover:text-white transition-colors duration-200"
                  >
                    {link.label}
                  </a>
                ))}
              </div>
            )}
          </div>

          {/* Legal Notice */}
          <div className="mt-6 pt-6 border-t border-gray-800 text-xs text-gray-500 text-center">
            <p>{resolvedNotice}</p>
          </div>
        </div>
      </div>
    </footer>
  );
}
