import { useState, type FormEvent } from 'react';
import { Card } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Textarea } from './ui/textarea';
import { Button } from './ui/button';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from './ui/select';
import { Mail, MessageCircle, Phone, Send } from 'lucide-react';

type ContactSectionProps = {
  title?: string;
  subtitle?: string;
  whatsapp?: string;
  email?: string;
  phone?: string;
  hours?: string;
  privacyNote?: string;
};

export function ContactSection({
  title,
  subtitle,
  whatsapp,
  email,
  phone,
  hours,
  privacyNote,
}: ContactSectionProps) {
  const [formData, setFormData] = useState({
    nombre: '',
    apellido: '',
    rut: '',
    region: '',
    comuna: '',
    asunto: '',
    telefono: '',
    correo: '',
    comentarios: '',
  });

  const regiones = [
    'Arica y Parinacota',
    'Tarapaca',
    'Antofagasta',
    'Atacama',
    'Coquimbo',
    'Valparaiso',
    'Metropolitana',
    "O'Higgins",
    'Maule',
    'Nuble',
    'Biobio',
    'Araucania',
    'Los Rios',
    'Los Lagos',
    'Aysen',
    'Magallanes',
  ];

  const asuntos = [
    'Primera asesoria gratuita',
    'Informacion sobre el proceso',
    'Consulta sobre requisitos',
    'Revision de mi situacion financiera',
    'Agendar reunion presencial',
    'Otros',
  ];

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    // Aqui se enviaria el formulario
    console.log('Formulario enviado:', formData);
    alert('¡Gracias por tu interes! Te contactaremos pronto.');
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const resolvedTitle = title || '¡Analicemos tu caso!';
  const resolvedSubtitle =
    subtitle ||
    'Completa el formulario y te contactaremos para evaluar tu situacion sin ningun costo';
  const resolvedWhatsapp = whatsapp || '+56 9 1234 5678';
  const resolvedEmail = email || 'contacto@nomasarriendo.cl';
  const resolvedPhone = phone || '+56 2 2345 6789';
  const resolvedHours = hours || '';
  const resolvedPrivacyNote =
    privacyNote ||
    'Todos tus datos son tratados de forma confidencial y solo seran utilizados para contactarte y evaluar tu caso. No compartimos informacion con terceros.';

  return (
    <section id="contacto" className="py-16 lg:py-24 bg-brand-cream">
      <div className="container mx-auto px-6 lg:px-8">
        <div className="max-w-4xl mx-auto">
          {/* Header */}
          <div className="text-center mb-12">
            <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 mb-4">
              {resolvedTitle}
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              {resolvedSubtitle}
            </p>
          </div>

          <div className="grid lg:grid-cols-3 gap-8">
            {/* Contact Form */}
            <div className="lg:col-span-2">
              <Card className="p-8">
                <form onSubmit={handleSubmit} className="space-y-6">
                  {/* Personal Info Row */}
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="nombre">Nombre *</Label>
                      <Input
                        id="nombre"
                        required
                        value={formData.nombre}
                        onChange={(e) => handleInputChange('nombre', e.target.value)}
                        className="mt-1"
                      />
                    </div>
                    <div>
                      <Label htmlFor="apellido">Apellido *</Label>
                      <Input
                        id="apellido"
                        required
                        value={formData.apellido}
                        onChange={(e) => handleInputChange('apellido', e.target.value)}
                        className="mt-1"
                      />
                    </div>
                  </div>

                  {/* RUT */}
                  <div>
                    <Label htmlFor="rut">RUT *</Label>
                    <Input
                      id="rut"
                      required
                      placeholder="12.345.678-9"
                      value={formData.rut}
                      onChange={(e) => handleInputChange('rut', e.target.value)}
                      className="mt-1"
                    />
                  </div>

                  {/* Location Row */}
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="region">Region *</Label>
                      <Select onValueChange={(value) => handleInputChange('region', value)}>
                        <SelectTrigger className="mt-1">
                          <SelectValue placeholder="Selecciona tu region" />
                        </SelectTrigger>
                        <SelectContent>
                          {regiones.map((region) => (
                            <SelectItem key={region} value={region}>
                              {region}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="comuna">Comuna *</Label>
                      <Input
                        id="comuna"
                        required
                        placeholder="Ej: Las Condes, Providencia..."
                        value={formData.comuna}
                        onChange={(e) => handleInputChange('comuna', e.target.value)}
                        className="mt-1"
                      />
                    </div>
                  </div>

                  {/* Subject */}
                  <div>
                    <Label htmlFor="asunto">Motivo de consulta *</Label>
                    <Select onValueChange={(value) => handleInputChange('asunto', value)}>
                      <SelectTrigger className="mt-1">
                        <SelectValue placeholder="¿En que podemos ayudarte?" />
                      </SelectTrigger>
                      <SelectContent>
                        {asuntos.map((asunto) => (
                          <SelectItem key={asunto} value={asunto}>
                            {asunto}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  {/* Contact Info Row */}
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="telefono">Telefono *</Label>
                      <Input
                        id="telefono"
                        type="tel"
                        required
                        placeholder="+56 9 1234 5678"
                        value={formData.telefono}
                        onChange={(e) => handleInputChange('telefono', e.target.value)}
                        className="mt-1"
                      />
                    </div>
                    <div>
                      <Label htmlFor="correo">Correo electronico *</Label>
                      <Input
                        id="correo"
                        type="email"
                        required
                        value={formData.correo}
                        onChange={(e) => handleInputChange('correo', e.target.value)}
                        className="mt-1"
                      />
                    </div>
                  </div>

                  {/* Comments */}
                  <div>
                    <Label htmlFor="comentarios">Comentarios adicionales</Label>
                    <Textarea
                      id="comentarios"
                      placeholder="Cuentanos mas sobre tu situacion actual, tus objetivos, presupuesto estimado, etc."
                      value={formData.comentarios}
                      onChange={(e) => handleInputChange('comentarios', e.target.value)}
                      className="mt-1 min-h-[120px]"
                    />
                  </div>

                  {/* Submit Button */}
                  <Button
                    type="submit"
                    size="lg"
                    className="w-full bg-brand-brown hover:bg-brand-brown-dark text-white py-4 text-lg font-semibold shadow-lg hover:shadow-xl transition-all duration-200"
                  >
                    <Send className="w-5 h-5 mr-2" />
                    Enviar solicitud
                  </Button>

                  <p className="text-sm text-gray-500 text-center">
                    * Campos obligatorios. Al enviar este formulario aceptas que te contactemos para evaluar tu caso.
                  </p>
                </form>
              </Card>
            </div>

            {/* Contact Info Sidebar */}
            <div className="space-y-6">
              {/* Quick Contact */}
              <Card className="p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">
                  Contacto directo
                </h3>

                <div className="space-y-4">
                  <a
                    href={`https://wa.me/${resolvedWhatsapp.replace(/\D/g, '')}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center p-4 bg-green-50 border border-green-200 rounded-lg hover:bg-green-100 transition-colors duration-200"
                  >
                    <MessageCircle className="w-6 h-6 text-green-600 mr-3" />
                    <div>
                      <div className="font-semibold text-gray-900">WhatsApp</div>
                      <div className="text-sm text-gray-600">{resolvedWhatsapp}</div>
                    </div>
                  </a>

                  <a
                    href={`mailto:${resolvedEmail}`}
                    className="flex items-center p-4 bg-brand-beige border border-brand-tan rounded-lg hover:bg-brand-tan transition-colors duration-200"
                  >
                    <Mail className="w-6 h-6 text-brand-brown mr-3" />
                    <div>
                      <div className="font-semibold text-gray-900">Email</div>
                      <div className="text-sm text-gray-600">{resolvedEmail}</div>
                    </div>
                  </a>

                  <div className="flex items-center p-4 bg-gray-50 border border-gray-200 rounded-lg">
                    <Phone className="w-6 h-6 text-gray-600 mr-3" />
                    <div>
                      <div className="font-semibold text-gray-900">Telefono</div>
                      <div className="text-sm text-gray-600">{resolvedPhone}</div>
                    </div>
                  </div>
                </div>
              </Card>

              {/* Business Hours */}
              <Card className="p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">
                  Horarios de atencion
                </h3>

                {resolvedHours ? (
                  <div className="text-sm text-gray-600 whitespace-pre-line">
                    {resolvedHours}
                  </div>
                ) : (
                  <div className="space-y-2 text-sm text-gray-600">
                    <div className="flex justify-between">
                      <span>Lunes - Viernes:</span>
                      <span className="font-medium">9:00 - 18:00</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Sabados:</span>
                      <span className="font-medium">10:00 - 14:00</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Domingos:</span>
                      <span className="font-medium">Cerrado</span>
                    </div>
                  </div>
                )}

                <div className="mt-4 p-3 bg-brand-beige rounded-lg">
                  <p className="text-xs text-brand-brown-dark">
                    <strong>Respuesta garantizada:</strong> Te contactaremos dentro de las proximas 24 horas habiles.
                  </p>
                </div>
              </Card>

              {/* Privacy Note */}
              <Card className="p-6 bg-gray-50 border-gray-200">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">
                  Tu privacidad es importante
                </h3>
                <p className="text-sm text-gray-600 leading-relaxed">
                  {resolvedPrivacyNote}
                </p>
              </Card>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
